/**
 * Base class for ScriptContext, ContractContext, Scripts and other "macro"-types
 * @internal
 */
export class MacroType extends Common {
    /**
     * @type {string[]}
     */
    get fieldNames() {
        return [];
    }

    /**
     * @type {InstanceMembers}
     */
    get instanceMembers() {
        throw new Error("not yet implemented");
    }

    /**
     * @type {string}
     */
    get name() {
        throw new Error("not yet implemented");
    }

    /**
     * @type {null | HeliosDataClass<HeliosData>}
     */
    get offChainType() {
        return null;
    }

    /**
     * @type {string}
     */
    get path() {
        throw new Error("not yet implemented");
    }

	/**
	 * @type {TypeMembers}
	 */
	get typeMembers() {
        return {};
    }

    /**
     * @type {DataType}
     */
    get asDataType() {
        return this;
    }

    /**
     * @type {Named}
     */
    get asNamed() {
        return this;
    }

    /**
     * @type {Type}
     */
    get asType() {
        return this;
    }

    /**
     * @param {Site} site 
     * @param {InferenceMap} map 
     * @param {null | Type} type 
     * @returns {Type}
     */
    infer(site, map, type) {
        return this;
    }

    /**
     * @param {Type} other 
     * @returns {boolean}
     */
    isBaseOf(other) {
        throw new Error("not yet implemented");
    }

    /**
     * @returns {string}
     */
	toString() {
		return this.name;
	}

    /**
     * @returns {Typed}
     */
    toTyped() {
        return new DataEntity(this);
    }
}

/**
 * @internal
 * @typedef {{[name: string]: ScriptHashType}} ScriptTypes
 */

/**
 * @internal
 * @implements {DataType}
 */
export class ScriptsType extends MacroType {
    /**
     * @type {{[name: string]: Typed}}
     */
    #scripts;

    /**
     * @param {ScriptTypes} scripts 
     */
    constructor(scripts) {
        super();

        this.#scripts = {};
        
        for (let k in scripts) {
            this.#scripts[k] = scripts[k].toTyped();
        }
    }

    /**
     * @type {InstanceMembers}
     */
     get instanceMembers() {
        return {};
    }

    /**
     * @type {TypeMembers}
     */
    get typeMembers() {
        return {
            ...this.#scripts
        };
    }

    /**
     * @type {string}
     */
    get name() {
        return "Scripts";
    }

    /**
     * @type {string}
     */
    get path() {
        return "__helios__scripts";
    }

    /**
     * @param {Type} other 
     * @returns {boolean}
     */
    isBaseOf(other) {
        return other instanceof ScriptsType;
    }

    /**
     * @returns {boolean}
     */
    isEmpty() {
        return Object.keys(this.#scripts).length == 0;
    }
}

