		const head = (new Array(n)).fill("").map((_, i) => `head${i}`).join(", ");
		const unit = (new Array(n)).fill("").map((_, i) => "()").join(", ");
		const returnHead = n == 1 ? `${head}` : `(callback) -> {callback(${head})}`;

